import { FC, forwardRef } from 'react';

import {
  Wrapper,
  Caption,
  Copyright,
  FlushCaption,
  PaddedCaption,
  HeroCaption,
  CaptionText,
} from './MediaPlusCaption.styles';
import {
  TextProps,
  MediaPlusCaptionProps,
  MediaPlusCaptionForSliderProps,
} from './MediaPlusCaption.types';

const Text: FC<TextProps> = ({ caption = '', copyright }) => (
  <>
    {caption}
    {copyright && (
      <Copyright>
        {caption && ' '}({copyright})
      </Copyright>
    )}
  </>
);

export const MediaPlusFlushCaption: FC<MediaPlusCaptionProps> = ({
  caption,
  copyright,
  className,
  children,
}) => (
  <Wrapper className={className}>
    {children}
    {(caption || copyright) && (
      <FlushCaption>
        <Text caption={caption} copyright={copyright} />
      </FlushCaption>
    )}
  </Wrapper>
);

export const MediaPlusPaddedCaption: FC<MediaPlusCaptionProps> = ({
  caption,
  copyright,
  className,
  children,
}) => (
  <Wrapper className={className}>
    {children}
    {(caption || copyright) && (
      <PaddedCaption>
        <Text caption={caption} copyright={copyright} />
      </PaddedCaption>
    )}
  </Wrapper>
);

export const MediaPlusCaptionForHero: FC<MediaPlusCaptionProps> = ({
  caption,
  copyright,
  className,
  children,
}) => (
  <Wrapper className={className}>
    {children}
    {(caption || copyright) && (
      <HeroCaption>
        <Text caption={caption} copyright={copyright} />
      </HeroCaption>
    )}
  </Wrapper>
);

export const MediaPlusCaptionForSlider = forwardRef<
  HTMLDivElement,
  MediaPlusCaptionForSliderProps
>(function MediaPlusCaptionForSlider(
  { caption, copyright, className, children },
  ref,
) {
  return (
    <Wrapper className={className}>
      {children}
      <Caption ref={ref}>
        <CaptionText>
          <Text caption={caption} copyright={copyright} />
        </CaptionText>
      </Caption>
    </Wrapper>
  );
});
